import { Injectable } from '@angular/core';

import { User } from '../models';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable, of, interval } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
// import { TokenService } from './token.service';
//import { GlobalSettings } from '../global.settings';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  authbaseurl = 'http://master.portrayhomes.com/api/builder/login';

  constructor(private http: HttpClient, 
    private router: Router) {
  }

  public loginUser(email,password): Observable<any> {

    const formData = new FormData();
    formData.append('email', email);
    formData.append('password', password);

    return this.http.post(this.authbaseurl, formData)
    .pipe(
      tap(response => {
        console.log("responselogin",response);
        if(response['access_token']){
          localStorage.setItem("IFP_access_token",response['access_token']);
        }
        return response;
      }),
      catchError(this.handleError('login', []))
    );
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      return of(result as T);
    };
  }

  public login(): void {
    localStorage.setItem('token', 'token');
  }

  public sign(): void {
    localStorage.setItem('token', 'token');
  }

  public signOut(): void {
    localStorage.removeItem('token');
  }

  public getUser(): Observable<User> {
    return of({
      name: 'Admin ',
      lastName: 'Teacher'
    });
  }
}
