import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable, of, interval } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
// import { environment } from '../environments/environment';
// import { TokenService } from './token.service';
//import { GlobalSettings } from '../global.settings';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment';


@Injectable()
export class DataService {
   baseUrl: string = environment.builderBaseUrl;
  // private authbaseurl = environment.authUrl;

  authbaseurl = environment.builderBaseUrl+'builder/login';
  token = "";

  constructor(private http: HttpClient, 
    private router: Router) {
  }

  login(email, password): Observable<any> {

    const formData = new FormData();
    formData.append('email', email);
    formData.append('password', password);

    return this.http.post(this.authbaseurl, formData)
    .pipe(
      tap(response => {
        console.log("responselogin",response);
        if(response['access_token']){
          this.token = response['access_token'];
        }
        return response;
      }),
      catchError(this.handleError('login', []))
    );
  }

  addCommunity(url,data) : Observable<any> {

    
    this.token = localStorage.getItem("IFPTOken");
    console.log("token",this.token);

        const headers = new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': 'bearer '+this.token,
        });
      let options = { headers: headers };

    return this.http.post(url, data , options)
    .pipe(
      tap(response => {
        console.log("responselogin",response);
        if(response['access_token']){
          this.token = response['access_token'];
        }
        return response;
      }),
      catchError(this.handleError('login', []))
    );
  }
  
  getAll(url): Observable<any> {

    this.token = localStorage.getItem("IFPTOken");
    console.log("token",this.token);

        const headers = new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': 'bearer '+this.token,
        });
      let options = { headers: headers };

    return this.http.get(url,options)
        .pipe(
          tap(response => {
            return response;
          }),
          catchError(this.handleError('getHomeError', []))
        );
  }

  getAllHomes(): Observable<any> {

    this.token = localStorage.getItem("IFPTOken");
    console.log("token",this.token);

        const headers = new HttpHeaders({
          'Content-Type': 'application/json',
          'Authorization': 'bearer '+this.token,
        });
      let options = { headers: headers };

    return this.http.get("http://localhost:8000/api/builder/get-all-home",options)
        .pipe(
          tap(response => {
            return response;
          }),
          catchError(this.handleError('getHomeError', []))
        );
  }
  
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      return of(result as T);
    };
  }
} 