import {PreloadAllModules, RouterModule, Routes} from '@angular/router';
import { NgModule } from '@angular/core';
import { DashboardPageComponent,ProfilePageComponent,SupportPageComponent,TemplatePageComponent ,TutorialPageComponent} from './pages/dashboard/containers';

import { NotFoundComponent } from './pages/not-found/not-found.component';
import {AuthGuard} from './pages/auth/guards';

const routes: Routes = [
  {
    path: 'dashboard',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    component: DashboardPageComponent
  },
  {
    path: 'profile',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    component: ProfilePageComponent
  },
  {
    path: 'support',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    component: SupportPageComponent
  },
  {
    path: 'template',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    component: TemplatePageComponent
  },
  {
    path: 'tutorial',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    component: TutorialPageComponent
  },
  {
    path: 'typography',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    loadChildren: () => import('./pages/typography/typography.module').then(m => m.TypographyModule)
  },
  {
    path: 'new',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    loadChildren: () => import('./pages/typography-copy/typography.module').then(m => m.TypographyNewModule)
  },
  {
    path: 'community',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    loadChildren: () => import('./pages/community/community.module').then(m => m.CommunityModule)
  },
  {
    path: 'tables',
    // canActivate: [AuthGuard],
    loadChildren: () => import('./pages/tables/tables.module').then(m => m.TablesModule)
  },
  {
    path: 'com',
    // canActivate: [AuthGuard],
    loadChildren: () => import('./pages/tables/tables.module').then(m => m.TablesModule)
  },
  {
    path: 'notification',
    pathMatch: 'full',
    canActivate: [AuthGuard],
    loadChildren: () => import('./pages/notification/notification.module').then(m => m.NotificationModule)
  },
  {
    path: 'ui',
    canActivate: [AuthGuard],
    loadChildren: () => import('./pages/ui-elements/ui-elements.module').then(m => m.UiElementsModule)
  },
  {
    path: '404',
    component: NotFoundComponent
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/auth/auth.module').then(m => m.AuthModule)
  },
  {
    path: '**',
    redirectTo: '404'
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      useHash: true,
      preloadingStrategy: PreloadAllModules
    })
  ],
  exports: [RouterModule]
})

export class AppRoutingModule {
}
