import { Observable } from 'rxjs';

import { DashboardService } from '../../services';

import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import {
  DailyLineChartData,
  PerformanceChartData,
  ProjectStatData,
  RevenueChartData,
  ServerChartData,
  SupportRequestData,
  VisitsChartData
} from '../../models';
import { ToastrService } from 'ngx-toastr';

import { SuccessToastComponent } from '../../../notification/containers';
import { ErrorToastrComponent } from '../../../notification/containers';
import { InfoToastrComponent } from '../../../notification/containers';

enum ToastPositionTypes {
  bottomCenter = 'toast-bottom-center',
  bottomRight = 'toast-bottom-right',
  bottomLeft = 'toast-bottom-left',
  topCenter = 'toast-top-center',
  topRight = 'toast-top-right',
  topLeft = 'toast-top-left'
}

@Component({
  selector: 'app-profile-page',
  templateUrl: './profile-page.component.html',
  styleUrls: ['./profile-page.component.scss']
})
export class ProfilePageComponent {
  public dailyLineChartData$: Observable<DailyLineChartData>;
  public performanceChartData$: Observable<PerformanceChartData>;
  public revenueChartData$: Observable<RevenueChartData>;
  public serverChartData$: Observable<ServerChartData>;
  public supportRequestData$: Observable<SupportRequestData[]>;
  public visitsChartData$: Observable<VisitsChartData>;
  public projectsStatsData$: Observable<ProjectStatData>;
  public toastrPositionTypes: typeof ToastPositionTypes = ToastPositionTypes;
  public toastrPosition: string = this.toastrPositionTypes.topRight;
  public timeOut = 3000;
  getProfileUrl = "builder/profile";
  public form: FormGroup;
  profile = {
    id: 1,
    parent_ref_id: 0,
    first_name: "santosh",
    last_name: "dangare",
    email: "builder@gmail.com",
    email_verified_at: null,
    mobile: 8411825783,
    company: "synextic",
    address: "abad",
    role: "MB",
    status: "Active",
    created_at: "2020-11-03T09:39:34.000000Z",
    updated_at: "2020-11-03T09:39:34.000000Z"
}

  constructor(private service: DashboardService,private toastrService: ToastrService) {
    this.dailyLineChartData$ = this.service.loadDailyLineChartData();
    this.performanceChartData$ = this.service.loadPerformanceChartData();
    this.revenueChartData$ = this.service.loadRevenueChartData();
    this.serverChartData$ = this.service.loadServerChartData();
    this.supportRequestData$ = this.service.loadSupportRequestData();
    this.visitsChartData$ = this.service.loadVisitsChartData();
    this.projectsStatsData$ = this.service.loadProjectsStatsData();

    this.service.simplePost(this.getProfileUrl).subscribe(result => {
      console.log("Profile....",result.data);
     });
  }
  
  public ngOnInit(): void {
    this.form = new FormGroup({
      first_name: new FormControl(this.profile.first_name, [Validators.required,]),
      last_name: new FormControl(this.profile.last_name, [Validators.required]),
      mobile: new FormControl(this.profile.mobile, [Validators.required]),
      address: new FormControl(this.profile.address, [Validators.required]),
      company: new FormControl(this.profile.company, [Validators.required])
    });
  }

  saveProfile (){
    this.showSuccess();
  }

  public showSuccess(): void {
    this.toastrService.show(
      null,
      null,
      {
        positionClass: this.toastrPosition,
        toastComponent: SuccessToastComponent,
        timeOut: this.timeOut,
        tapToDismiss: false
      }
    );
  }
 
}
