import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthService } from '../../services/auth.service';


@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})
export class LoginFormComponent implements OnInit {
  @Output() sendLoginForm = new EventEmitter<void>();
  public form: FormGroup;
  public flatlogicEmail = 'test@gmail.com';
  public flatlogicPassword = '1234';

  constructor(private authService:AuthService) {}

  public ngOnInit(): void {
    this.form = new FormGroup({
      email: new FormControl(this.flatlogicEmail, [Validators.required, Validators.email]),
      password: new FormControl(this.flatlogicPassword, [Validators.required])
    });
  }

  public login(): void {

   let email =  this.form.get('email').value;
   let password = this.form.get('password').value;

    this.authService.loginUser(email,password).subscribe(result => {
      console.log("result....",result);
      localStorage.setItem("IFPToken",result['access_token']);
      if (this.form.valid) {
        this.sendLoginForm.emit();
      }
     });
    
  }
}
